



























































import { Vue, Component } from 'vue-property-decorator'
import NewLineGraph from "@/components/NewLineGraph.vue"

@Component({components: {NewLineGraph}})
export default class Home extends Vue {
    toggle_size: number = 1

    formatTime(date: Date) {
        return (date.getHours() < 10? "0" + date.getHours(): date.getHours())
            + ":" + (date.getMinutes() < 10? "0" + date.getMinutes(): date.getMinutes())
            + ":" + (date.getSeconds() < 10? "0" + date.getSeconds(): date.getSeconds())
    }

    formatDuration(time: number) {
        const days = Math.floor(time/(3600*24))
        const hours = Math.floor((time - (days*3600*24))/3600)
        const minutes = Math.floor((time - (days*3600*24) - (hours*3600)) / 60)
        const seconds = (time - (days*3600*24) - (hours*3600) - (minutes*60))
        return days + " d " + (hours < 10? "0" + hours: hours) + "h " +
            (minutes < 10? "0" + minutes: minutes) + "m " +
            (seconds < 10? "0" + seconds.toPrecision(5): seconds.toPrecision(5)) + "s"
    }

    get hasData() {
        return Object.keys(this.$store.state.epochs).length > 0
    }

    get num_batch_for_epoch() {
        if (this.hasData) {
            const last_epoch = Object.keys(this.$store.state.epochs)[Object.keys(this.$store.state.epochs).length - 1]
            return this.$store.state.epochs[last_epoch].batches.length
        } else {
            return 0
        }
    }

    get progress() {
        if (this.hasData) {
            const total_batches_done =  ((Object.keys(this.$store.state.epochs).length - 1)
                * this.$store.state.info.batches_per_epoch) + this.num_batch_for_epoch
            return (total_batches_done / (this.$store.state.info.batches_per_epoch * this.$store.state.info.total_epochs))*100
        } else {
            return 0
        }
    }

    get avg_time_per_epoch() {
        if (this.hasData) {
            const total_batches_done =  ((Object.keys(this.$store.state.epochs).length - 1)
                * this.$store.state.info.batches_per_epoch) + this.num_batch_for_epoch
            return Object.keys(this.$store.state.epochs).reduce((acc: any, epoch) => {
                this.$store.state.epochs[epoch].batches.forEach((batch: any) => {
                    acc += batch.time
                })
                return acc
            }, 0) / total_batches_done
        } else {
            return 0
        }
    }

    get time_left() {
        if (this.hasData) {
            const total_batches_done =  ((Object.keys(this.$store.state.epochs).length - 1)
                * this.$store.state.info.batches_per_epoch) + this.num_batch_for_epoch
            const batches_left = (this.$store.state.info.batches_per_epoch * this.$store.state.info.total_epochs) -
                total_batches_done
            return batches_left*this.avg_time_per_epoch
        } else {
            return 0
        }
    }

    get acc_data() {
        return [Object.keys(this.$store.state.epochs).reduce((acc, epoch) => {
            //@ts-ignore
            acc.y.push(this.$store.state.epochs[epoch].accuracy)
            //@ts-ignore
            acc.x.push(this.$store.state.epochs[epoch].id)
            return acc
        }, {x: [0], y:[0]})]
    }

    get loss_data() {
        return [Object.keys(this.$store.state.epochs).reduce((acc, epoch) => {
            //@ts-ignore
            acc.y.push(this.$store.state.epochs[epoch].loss)
            //@ts-ignore
            acc.x.push(this.$store.state.epochs[epoch].id)
            return acc
        }, {x: [], y:[]})]
    }

    get eval_acc_data() {
        return [Object.keys(this.$store.state.epochs)
            .slice(0, Object.keys(this.$store.state.epochs).length - 1).reduce((acc, epoch) => {
            //@ts-ignore
            acc.y.push(this.$store.state.epochs[epoch].eval_accuracy)
            //@ts-ignore
            acc.x.push(this.$store.state.epochs[epoch].id)
            return acc
        }, {x: [0], y:[0]})]
    }

    get eval_loss_data() {
        return [Object.keys(this.$store.state.epochs)
            .slice(0, Object.keys(this.$store.state.epochs).length - 1).reduce((acc, epoch) => {
            //@ts-ignore
            acc.y.push(this.$store.state.epochs[epoch].eval_loss)
            //@ts-ignore
            acc.x.push(this.$store.state.epochs[epoch].id)
            return acc
        }, {x: [], y:[]})]
    }

    get acc_batch() {
        return [Object.keys(this.$store.state.epochs).reduce((acc: any, epoch) => {
            this.$store.state.epochs[epoch].batches.forEach((batch: any) => {
                acc.y.push(batch.accuracy)
                acc.x.push(batch.global_id)
            })
            return acc
        }, {x: [], y:[]})]
    }

    get loss_batch() {
        return [Object.keys(this.$store.state.epochs).reduce((acc: any, epoch) => {
            this.$store.state.epochs[epoch].batches.forEach((batch: any) => {
                acc.y.push(batch.loss)
                acc.x.push(batch.global_id)
            })
            return acc
        }, {x: [], y:[]})]
    }
}
