











import { Vue, Component } from 'vue-property-decorator'
import gql from "graphql-tag";

@Component
export default class App extends Vue {

    created() {
        this.$store.dispatch("fetchData")
    }

    mounted() {
        //@ts-ignore
        this.$apollo.addSmartSubscription('tagAdded', {
            query: gql`subscription epochs {
                update {
                    epoch {
                        id, accuracy, loss, batches {
                            id, accuracy, loss, global_id, time
                        }, eval_loss, eval_accuracy
                    },
                    info {
                        duration
                    }
                }
              }`,
            result(data: any) {
                this.$store.commit("setInfo", data.data.update.info)
                this.$store.commit("updateEpoch", data.data.update.epoch)
            },
        })
    }
}
